#contactpage {
    .contactpage {
        min-height: 100vh;
        display: flex;
        justify-content: center;
        padding: 150px 0 0 0;
        a {
            background: rgb(186, 179, 245);
            height: 44px;
            padding: 10px 20px;
            font-weight: 900;
            border-radius: 7px;
            -webkit-border-radius: 7px;
            -moz-border-radius: 7px;
            -ms-border-radius: 7px;
            -o-border-radius: 7px;
            transition: 0.4s;
            -webkit-transition: 0.4s;
            -moz-transition: 0.4s;
            -ms-transition: 0.4s;
            -o-transition: 0.4s;
            &:hover {
                color: rgb(25, 0, 255);
                background: rgb(179, 227, 245);
            }
        }
    }
}
